	module.exports = {"data":{"post":{"html":"<p>Yo, still here. Christopher is home sick, and while Oma has been able to stay with him it still throws a stick into the spokes of our schedule. Plus this is the week of reckoning at work, as the dreaded 6-weeks-before-the-end-of-the-quarter deadline for terminating employment contracts looms. An all company meeting is set for Friday. The rumors are flying, since everyone knows we haven't landed any new contracts, and I remain cautiously pessimistic. Mama's employer isn't doing that well either, but since it is a Dow Jones company everyone hears about their problems. So she's busy cutting costs too.</p>\n<p>Meanwhile, Mama was pictured last week in the <a href=\"http://www.bild.de\">Bild Zeitung</a>. No, not as a Bild Girl, but together with her boss as a promo for her employer. And only in the Hannover edition.</p>","fields":{"title":"On The Cover of the Rolling Stone","date":"13 Nov 2002","url":"https://papascott.de/2002-11-13-on-the-cover-of-the-rolling-stone/"}}},"pathContext":{"slug":"/2002-11-13-on-the-cover-of-the-rolling-stone/","prev":{"fields":{"title":"Elected is elected, you can't fire me now","date":"13 Nov 2002","slug":"/2002-11-13-elected-is-elected-you-cant-fire-me-now/"}},"next":{"fields":{"title":"My Domain Got Farked!","date":"14 Nov 2002","slug":"/2002-11-14-my-domain-got-farked/"}}}}

